.new-order-container {
  max-width: 480px;
  margin: 32px auto;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 16px rgba(0,0,0,0.07);
  padding: 32px 24px 24px 24px;
}

.new-order-title {
  text-align: center;
  color: #222;
  margin-bottom: 24px;
  font-size: 1.6rem;
  font-weight: bold;
}

.section-label {
  font-weight: bold;
  color: #007b55;
  margin-bottom: 8px;
  display: block;
}

.service-section {
  margin-bottom: 24px;
}

.service-options {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.service-card {
  background: #f5f5f5;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  padding: 12px 20px;
  cursor: pointer;
  font-weight: 500;
  transition: border 0.2s, background 0.2s;
  user-select: none;
}
.service-card.selected {
  border: 2px solid #00c853;
  background: #e8f5e9;
  color: #007b55;
}

.vehicle-section, .customer-section {
  margin-bottom: 24px;
}

.vehicle-fields {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.input-field {
  padding: 10px 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 1rem;
  margin-bottom: 0;
}

.submit-btn {
  width: 100%;
  background: #00c853;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 14px 0;
  font-size: 1.1rem;
  font-weight: bold;
  margin-top: 18px;
  cursor: pointer;
  transition: background 0.2s;
}
.submit-btn:disabled {
  background: #bdbdbd;
  cursor: not-allowed;
}

.order-summary {
  margin-top: 32px;
  background: #f9fbe7;
  border: 1px solid #cddc39;
  border-radius: 8px;
  padding: 18px 16px;
  color: #333;
}
.order-summary h3 {
  margin-top: 0;
  color: #7cb342;
} 