.settings-container {
  max-width: 600px;
  margin: 32px auto;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 16px rgba(0,0,0,0.07);
  padding: 32px 24px 24px 24px;
}
.settings-container h2 {
  text-align: center;
  color: #222;
  margin-bottom: 18px;
}
.settings-container h3 {
  color: #007b55;
  margin-bottom: 12px;
}
.add-service-form {
  display: flex;
  gap: 12px;
  margin-bottom: 24px;
}
.add-service-form input {
  padding: 8px 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 1rem;
}
.add-service-form button {
  background: #00c853;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 8px 18px;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.2s;
}
.add-service-form button:hover {
  background: #009624;
}
.services-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 8px;
}
.services-table th, .services-table td {
  border: 1px solid #e0e0e0;
  padding: 10px 8px;
  text-align: left;
}
.services-table th {
  background: #f5f5f5;
}
.services-table tr:nth-child(even) {
  background: #fafafa;
}
.services-table button {
  margin-right: 6px;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 4px 10px;
  font-size: 0.95rem;
  cursor: pointer;
  transition: background 0.2s;
}
.services-table button:last-child {
  background: #d32f2f;
}
.services-table button:hover {
  opacity: 0.85;
} 